
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#pay .header {
  height: 56px;
  overflow: hidden;
  padding: 0 10px;
}
#pay .header-l {
  margin-top: 15px;
  overflow: hidden;
}
#pay .header-l .game-b,
#pay .header-l .reload {
  float: left;
}
#pay .header-l .game-b {
  padding: 5px;
  background: #fef7e7;
  border-radius: 5px;
}
#pay .header-l .reload {
  margin-top: 5px;
  margin-left: 5px;
}
#pay .header-r {
  margin: 10px 0;
}
#pay .header-r button {
  background: #efa500;
  height: 35px;
  line-height: 35px;
  border: none;
}
#pay .body {
  border-top: 1px solid #ddd;
  height: 500px;
  text-align: center;
  position: relative;
}
#pay .body .ads {
  margin-top: 10px;
}
#pay .body .start-box {
  position: absolute;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: #fcc505;
  font-size: 24px;
  left: 50%;
  top: 50%;
  margin-left: -100px;
  margin-top: -100px;
}
#pay .body .start-box .start {
  margin-top: 60px;
}
#pay .body .start-box .b {
  margin-top: 20px;
}
#pay .body .start-box .alert {
  font-size: 16px;
  margin-top: 70px;
}
#pay .footer {
  border-top: 1px solid #ddd;
  text-align: center;
  padding: 10px 0;
  height: 40px;
}
#pay .footer button {
  width: 40%;
  margin-left: 10px;
  height: 40px;
  line-height: 40px;
  border: none;
  display: inline-block;
}
#pay .footer button:focus {
  outline: none;
}
#pay .footer .change {
  margin-left: -5px;
  border: 1px solid #eee;
  color: #333;
  background-image: linear-gradient(to bottom, #fbfbfb, #e1e1e1);
}
#pay .footer .weui-btn_primary {
  margin-top: 0;
}



